import React, { useRef } from "react";
import {
    Grid,
    Box,
    Card,
    Typography,
    Alert,
    Skeleton,
    Divider,
} from "@mui/material";
import useGetAttendee from "../../hooks/useGetAttendee";
import "react-toastify/dist/ReactToastify.css";
import { ReactComponent as AccessCheck } from "../../assets/images/accessCheck.svg";
import { ReactComponent as AccessGranted } from "../../assets/images/accessGranted.svg";
import { ReactComponent as AccessDenied } from "../../assets/images/accessDenied.svg";
import Stack from "@mui/material/Stack";
import { useOutletContext } from "react-router-dom";
import Search from "../Home/Search";
import QrCodeScannerIcon from "@mui/icons-material/QrCodeScanner";
import useScanInput from "../../hooks/useScanInput";

function SelfService(props) {
    console.log("rerender");
    const { searchOpen, setSearchOpen } = useOutletContext();
    const heroImage = useRef(<AccessCheck />);
    const alerts = useRef([]);

    const {
        hash,
        attendee,
        loading,
        setHash,
        checkAutoCheckin,
        checkAutoBadgePrint,
    } = useGetAttendee(false);

    //Honeywell scanner input
    const { input } = useScanInput();

    React.useEffect(() => {
        setHash(input);
        // eslint-disable-next-line
    }, [input]);

    // Alerts

    function addAlert(type, description) {
        alerts.current.push(
            <Alert key={Date.now() + description} severity={type}>
                {description}
            </Alert>
        );
    }

    React.useEffect(() => {
        alerts.current = [];

        // Checkin
        var checkinStatus = checkAutoCheckin();
        if (checkinStatus?.status === "success") {
            console.log("Set Hero Success");
            heroImage.current = <AccessGranted />;
            addAlert(checkinStatus?.status, checkinStatus?.description);
        } else if (checkinStatus?.status === "error") {
            heroImage.current = <AccessDenied />;
            console.log("Set Hero Denied");
            addAlert(checkinStatus?.status, checkinStatus?.description);
        }

        // BadgePrint

        var printStatus = checkAutoBadgePrint();
        if (printStatus) {
            addAlert(printStatus.status, printStatus.description);
        }

        // eslint-disable-next-line
    }, [attendee]);

    return (
        <>
            <Box sx={{ m: { xs: 1, lg: 3 } }}>
                <Grid container spacing={4}>
                    <Grid item xs={12} sm={6}>
                        <Box
                            sx={{
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                                p: { xs: 0, lg: 5 },
                            }}>
                            {heroImage.current}
                        </Box>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <Card
                            sx={{
                                height: "100%",
                                display: "flex",
                                flexDirection: "column",
                            }}>
                            {attendee || loading ? (
                                <Box
                                    sx={{
                                        display: "flex",
                                        alignItems: "center",
                                        justifyContent: "center",
                                        flexDirection: "column",
                                        flexGrow: 1,
                                        m: 3,
                                    }}>
                                    <Typography
                                        sx={{
                                            fontSize: {
                                                sx: 36,
                                                md: 48,
                                                lg: 64,
                                            },
                                        }}
                                        variant="h1">
                                        {loading ? (
                                            <Skeleton width={200} />
                                        ) : (
                                            attendee?.data.attendeeFirstName +
                                            " " +
                                            attendee?.data.attendeeLastName
                                        )}
                                    </Typography>
                                    <Typography
                                        sx={{
                                            fontSize: {
                                                sx: 16,
                                                md: 24,
                                                lg: 28,
                                            },
                                        }}>
                                        {loading ? (
                                            <Skeleton width={100} />
                                        ) : (
                                            hash
                                        )}
                                    </Typography>
                                </Box>
                            ) : (
                                <Box
                                    sx={{
                                        display: "flex",
                                        alignItems: "center",
                                        justifyContent: "space-between",
                                        flexDirection: "column",
                                        flexGrow: 1,
                                        m: { xs: 2, lg: 3 },
                                    }}>
                                    <QrCodeScannerIcon
                                        sx={{
                                            fontSize: 120,
                                            flexGrow: 1,
                                            m: 2,
                                        }}
                                    />
                                    <Box>
                                        <Typography
                                            sx={{
                                                fontSize: {
                                                    xs: 24,
                                                    md: 30,
                                                    lg: 36,
                                                },
                                                lineHeight: 1.5,
                                                textAlign: "center",
                                            }}>
                                            Scanne den QR-Code
                                        </Typography>
                                        <Divider
                                            sx={{ bgcolor: "primary" }}
                                            flexItem
                                        />
                                        <Typography
                                            sx={{
                                                fontSize: {
                                                    xs: 18,
                                                    md: 24,
                                                    lg: 30,
                                                },
                                                lineHeight: 1.5,
                                                textAlign: "center",
                                                color: "text.secondary",
                                            }}>
                                            Der QR-Code befindet sich auf dem
                                            Ticket
                                        </Typography>
                                    </Box>
                                </Box>
                            )}
                            <Stack sx={{ width: "100%" }} spacing={2}>
                                {alerts.current}
                            </Stack>
                        </Card>
                    </Grid>
                </Grid>
            </Box>
            <Search
                processInput={setHash}
                searchOpen={searchOpen}
                setSearchOpen={setSearchOpen}
            />
        </>
    );
}

export default SelfService;
