import React, { useState } from "react";

function useScanInput() {
    const [input, setInput] = useState(undefined);

    const scannerInput = (event) => {
        console.log("A key was pressed", event.keyCode);
    };

    React.useEffect(() => {
        var scanInput = "";
        var scanActive = false;

        window.addEventListener("keydown", function (e) {
            const textInput = e.key || String.fromCharCode(e.keyCode);
            const targetName = e.target.localName;
            if (textInput === "!") {
                scanActive = true;
            } else if (textInput === "?") {
                e.preventDefault();
                scanActive = false;
                console.log("New Scan: " + scanInput);
                var startChar = scanInput.indexOf("doo.net/checkin/") + 16;
                setInput(scanInput.substring(startChar, startChar + 8));
                scanInput = "";
            } else if (
                scanActive &&
                textInput &&
                textInput.length === 1 &&
                targetName !== "input"
            ) {
                e.preventDefault();
                scanInput = scanInput + textInput;
            }
        });

        // cleanup this component
        return () => {
            window.removeEventListener("keydown", scannerInput);
        };
    }, []);

    return { input };
}

export default useScanInput;
