import { useEffect, useState } from "react";
import axios from "axios";
import configData from "../config.json";
import useLocalStorage from "./useLocalStorage";
import { validateRight } from "../utils/utils";
import printJS from "print-js";
import useZebra from "./useZebra";
import { toast } from "react-toastify";

function useGetAttendee(initAttendee) {
    console.log("rerender useGetAttendee");
    const { printLabel } = useZebra();

    const { value: zebraActivated } = useLocalStorage("zebraActivated", false);
    const { value: autoCheckinActivated } = useLocalStorage(
        "autoCheckinActivated",
        false
    );

    const { value: autoBadgePrintActivated } = useLocalStorage(
        "autoBadgePrintActivated",
        false
    );

    const [hash, setHash] = useState(null);
    const [attendee, setAttendee] = useState(
        initAttendee ? configData.getAttendee : undefined
    );
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    // Get Attendee
    useEffect(() => {
        var webhookURL = localStorage.getItem("webhookURL");

        if (!webhookURL) {
            toast.warn(
                "Der Teilnehmer kann nicht abgerufen werden. Es ist kein Verbindungsschlüssel hinterlegt."
            );
        }

        if (hash && webhookURL) {
            setLoading(true);
            console.log("Get attendee: " + hash);
            var url = `https://hook.doo.integromat.celonis.com/${webhookURL}?request=getAttendee&hash=${hash}`;
            axios
                .get(url)
                .then((response) => {
                    setAttendee(response.data);
                })
                .catch((err) => {
                    setError(err.response.data.error);
                    toast.error(err.response.data.error);
                })
                .finally(() => {
                    setLoading(false);
                });
        }
        // eslint-disable-next-line
    }, [hash]);

    function checkAutoCheckin() {
        console.log("validate and check print");
        if (autoCheckinActivated && attendee) {
            console.log("AutoCheckin");
            var validRights = attendee.rights?.filter((item) => {
                return validateRight(item, attendee.data.checkIns);
            });

            if (validRights.length === 0) {
                return {
                    status: "error",
                    description: "Teilnehmer nicht berechtigt",
                };
            } else if (validRights.length === 1) {
                checkin(attendee.ticketHash, validRights[0].TidPid);
                return {
                    status: "success",
                    description: "Erfolgreich eingecheckt",
                };
            } else if (validRights.length > 0) {
                return {
                    status: "warning",
                    description:
                        "Es stehen mehrere gültige Rechte zur Auswahl.",
                };
            }
        }
    }

    function checkin(attendeeHash, identifier) {
        var url = `https://hook.doo.integromat.celonis.com/${localStorage.getItem(
            "webhookURL"
        )}?request=checkinAttendee&hash=${attendeeHash}&identifier=${identifier}`;
        console.log("Checkin attendee: " + url);
        axios
            .get(url)
            .then((response) => {
                if (response.data.status === "success") {
                    console.log("Teilnehmer erfolgreich eingecheckt");
                } else {
                    console.log("Teilnehmer konnte nicht eingecheckt werden");
                }
            })
            .catch((err) => {
                console.log(err);
            });
    }

    function checkAutoBadgePrint() {
        console.log("checkAutoBadgePrint");
        if (attendee?.data?.defaultBadgePrinted) {
            return {
                status: "warning",
                description: "Badge wurde bereits gedruckt",
            };
        } else if (
            autoBadgePrintActivated &&
            !attendee?.data?.defaultBadgePrinted &&
            attendee?.data?.defaultBadgeURL
        ) {
            try {
                console.log("Print: " + attendee.data.defaultBadgeURL);
                if (zebraActivated) {
                    printLabel(
                        attendee.data.defaultBadgeURL,
                        attendee.ticketHash
                    );
                    return {
                        status: "success",
                        description: "Badge wird gedruckt",
                    };
                } else {
                    printJS({
                        printable: attendee.data.defaultBadgeURL,
                        type: "image",
                    });
                }
            } catch (e) {
                return {
                    status: "error",
                    description: "Badge konnte nicht gedruckt werden",
                };
            }
        }
    }

    return {
        hash,
        attendee,
        loading,
        error,
        setHash,
        checkAutoCheckin,
        checkAutoBadgePrint,
    };
}

export default useGetAttendee;
