import React, { useRef } from "react";
import useFetch from "../../hooks/useFetch";
import Drawer from "@mui/material/Drawer";
import {
    Button,
    Card,
    Stack,
    TextField,
    Typography,
    Skeleton,
    Box,
} from "@mui/material";
import IconButton from "@mui/material/IconButton";
import CloseIcon from "@mui/icons-material/Close";
import { SidebarWidth } from "../../assets/global/Theme-variable";

function Search(props) {
    const { data, loading, refetch, setData } = useFetch(undefined);
    const inputRef = useRef();

    const formattedSearchResults = data?.results?.map((item) => {
        return (
            <Card
                variant="outlined"
                key={item.key}
                sx={{
                    p: 0,
                    width: "100%",
                    borderRadius: "5px",
                }}>
                <Box
                    p={2}
                    display="flex"
                    alignItems="center"
                    justifyContent="space-between">
                    <Box>
                        <Typography variant="body1" gutterBottom>
                            {item.data.attendeeFirstName +
                                " " +
                                item.data.attendeeLastName}
                        </Typography>
                        <Typography
                            color="textSecondary"
                            variant="body2"
                            sx={{
                                fontWeight: "400",
                                fontSize: "13px",
                            }}>
                            {item.key}
                        </Typography>
                    </Box>
                    <Button
                        variant="outlined"
                        size="small"
                        onClick={(e) => {
                            props.processInput(item.key);
                            inputRef.current.value = "";
                            setData(null);
                            props.setSearchOpen(false);
                        }}>
                        Anzeigen
                    </Button>
                </Box>
            </Card>
        );
    });

    async function search(e) {
        if (
            !e.target.value.startsWith("/") &&
            e.target.value.length >= 3 &&
            localStorage.getItem("webhookURL")
        ) {
            console.log("Suche: " + e.target.value);
            refetch("search", undefined, e.target.value);
        } else {
            setData(null);
        }
    }

    function onSubmit(e) {
        e.preventDefault();
        console.log("Submit: " + inputRef.current.value);
        if (inputRef.current.value.startsWith("/")) {
            props.processInput(inputRef.current.value);
            setData(null);
            inputRef.current.value = "";
        }
    }

    return (
        <Drawer
            anchor="right"
            open={props.searchOpen}
            onClose={() => props.setSearchOpen(false)}
            PaperProps={{
                sx: {
                    border: 1,
                    borderColor: "info.light",
                    width: SidebarWidth,
                    height: `calc(100vh - 32px)`,
                    "&::-webkit-scrollbar": { display: "none" },
                    m: "16px",
                    p: 2,
                    borderRadius: "10px",
                    backdropFilter: `saturate(200%) blur(16px)`,
                },
            }}>
            <Stack spacing={2}>
                <IconButton
                    aria-label="close"
                    onClick={() => props.setSearchOpen(false)}
                    sx={{
                        position: "absolute",
                        right: 1,
                        top: 1,
                    }}>
                    <CloseIcon />
                </IconButton>
                <Box>
                    <Typography variant="h4">Suche</Typography>
                    <Typography variant="body2" color="textSecondary">
                        Lorem ipsum dolor sit amet, consetetur sadipscing elitr,
                        sed diam nonumy eirmod tempor.
                    </Typography>
                </Box>
                <form onSubmit={onSubmit}>
                    <TextField
                        id="search"
                        label="Suche"
                        onKeyUp={(e) => search(e)}
                        variant="outlined"
                        size="small"
                        inputProps={{ ref: inputRef }}
                        fullWidth
                    />
                </form>
                {formattedSearchResults}
                {loading && <Skeleton />}
            </Stack>
        </Drawer>
    );
}

export default Search;
